@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .def-font {
    font-family: "Neucha", "PT Sans", sans-serif;
    font-style: normal;
    font-weight: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-font-kerning: normal;
    -webkit-text-size-adjust: 100%;
  }

  .def-body {
    transform-style: preserve-3d;
    transform: perspective(60rem);
  }

  .transform-ul {
    transform: rotateX(-10deg) rotateY(20deg);
  }

  .transition-bef {
    transition: transform 0.3s cubic-bezier(0.16, 1.08, 0.38, 0.98);
  }

  .transition-mask {
    transition: all 0.8s cubic-bezier(0.16, 1.08, 0.38, 0.98);
  }

  .transform-hov {
    transform: skewX(12deg) translateX(5px);
  }

  .transform-hov-second {
    transform: skewX(12deg) translateX(-5px);
  }

  .nav-bg {
    background-image: url("/src/img/nav-back.svg");
    background-size: 100%;
  }

  .h2-style {
    @apply lg:text-5xl sm:text-3xl text-2xl font-bold text-center lg:mb-12 mb-4;
  }

  .icon {
    @apply  lg:w-16 lg:h-16 sm:w-12 sm:h-12 w-10 h-10 relative z-10 translate-x-3 grayscale;
  }
  .social-text {
    @apply absolute sm:text-2xl text-lg text-white lg:top-[23px] sm:top-[15px] top-[10px] z-0 group-hover:animate-socialAnim translate-x-[-100%] sm:group-hover:translate-x-[80px] group-hover:translate-x-[60px] block whitespace-nowrap group-focus-visible:animate-socialAnim sm:group-focus-visible:translate-x-[80px] group-focus-visible:translate-x-[60px] focus-visible:outline-none;
  }

  .animate-alternate {
    animation-direction: alternate;
  }

  .bg-form {
    background-image: url("/src/img/form-back.jpg")
  }

  @keyframes flyCycle {
    100% {
      background-position: -900px 0;
    }
  }

  .bird {
    background-image: url("/src/img/flying-bird.svg");
    background-size: auto 100%;
    width: 88px;
    height: 125px;

    animation-name: flyCycle;
    animation-timing-function: steps(10);
    animation-iteration-count: infinite;
  }

  .bird--one {
    animation-duration: 1s;
    animation-delay: -0.5s;
  }

  .bird--two {
    animation-duration: 0.9s;
    animation-delay: -0.75s;
  }

  .bird--three {
    animation-duration: 1.25s;
    animation-delay: -0.25s;
  }

  .bird--four {
    animation-duration: 1.1s;
    animation-delay: -0.5s;
  }


  @keyframes flyBird {
    0% {
      transform: scale(0.3) translateX(0vw);
    }
    
    12.5% {
      transform: translateY(2vh) translateX(6vw) scale(0.4);
    }
    
    25% {
      transform: translateY(0vh) translateX(12vw) scale(0.5);
    }
    
    37.5% {
      transform: translateY(4vh) translateX(18vw) scale(0.6);
    }
    
    50% {
      transform: translateY(2vh) translateX(24vw) scale(0.6);
    }
    
    62.5% {
      transform: translateY(0vh) translateX(30vw) scale(0.6);
    }
    
    75% {
      transform: translateY(0vh) translateX(36vw) scale(0.5);
    }

    82.5% {
      transform: translateY(0vh) translateX(42vw) scale(0.5);
    }
    
    100% {
      transform: translateY(0vh) translateX(48vw) scale(0.4);
    }
  }

  @keyframes flyBirdMob {
    0% {
      transform: scale(0.3) translateX(0vw);
    }
    
    12.5% {
      transform: translateY(2vh) translateX(12.5vw) scale(0.4);
    }
    
    25% {
      transform: translateY(0vh) translateX(25vw) scale(0.5);
    }
    
    37.5% {
      transform: translateY(4vh) translateX(37.5vw) scale(0.6);
    }
    
    50% {
      transform: translateY(2vh) translateX(50vw) scale(0.6);
    }
    
    62.5% {
      transform: translateY(0vh) translateX(62.5vw) scale(0.6);
    }
    
    75% {
      transform: translateY(0vh) translateX(75vw) scale(0.5);
    }

    82.5% {
      transform: translateY(0vh) translateX(82.5vw) scale(0.5);
    }
    
    100% {
      transform: translateY(0vh) translateX(105vw) scale(0.4);
    }
  }

  .bird-container {
    position: absolute;    
    left: -10%;
    width: fit-content;
    transform: translateX(-50px) scale(0);
    animation-name: flyBird;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  .bird-container-mob {
    position: absolute;    
    left: -10%;
    width: fit-content;
    transform: translateX(-50px) scale(0);
    animation-name: flyBirdMob;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  .bird-container--one {
      animation-duration: 12s;
      animation-delay: 0;
      top: 40%;
    }

    .bird-container--two {
      animation-duration: 14s;
      animation-delay: 1s;
      top: 10%;
    }

    .bird-container--three {
      animation-duration: 12.6s;
      animation-delay: 4.5s;
      top: 50%;
    }

    .bird-container--four {
      animation-duration: 16s;
      animation-delay: 3.25s;
      top: 35%;
    }

    .form-item {
      @apply hover:outline-none focus-visible:outline-none hover:bg-yellow-100 focus-visible:bg-yellow-100 resize-none
    }
    
    .social-link {
      @apply xl:w-[70%] w-full rounded-full bg-red-400 relative block overflow-hidden p-1 shadow-custom;
    }

   
}

@font-face {
  font-family: 'Neucha';
  src: url('/src/fonts/Neucha.woff');
  src: local('Neucha'),
    url('/src/fonts/Neucha.eot?#iefix') format('embedded-opentype'),
    url('/src/fonts/Neucha.woff') format('woff'),
    url('/src/fonts/Neucha.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}